.hero {
    background-color: var(--main-background-color);
}

.hero--container {
    position: relative;
    overflow: hidden;

    @include mq('mobile-only') {
        max-width: 480px;
        height: 444px;
    }

    @include mq('tablet') {
        width: 768px;
        height: 416px;
        padding-top: 82px;
    }

    @include mq('desktop') {
        width: 1200px;
        height: 664px;
        padding-top: 82px;
    }

}


.hero__container {

    position: absolute;
    bottom: 158px;
    left: 20px;
    z-index: 20;

    display: inline-block;

    @include mq('mobile-only') {
        width: 216px
    }

    @include mq('tablet') {
        width: 162px;
        top: 102px;
        left: 32px;

        animation: Appereance-container-tablet 2s 1 ease-in-out;
    }

    @include mq('desktop') {
        width: 277px;
        top: 147px;
        left: 16px;

        animation: Appereance-container-desktop 2s 1 ease-in-out;
    }
}

.hero__title {

    font-family: var(--secondary-font);

    font-size: 26px;
    line-height: 1.145;
    text-transform: uppercase;

    color: var(--white-color);

    margin-bottom: 20px;

    @include mq('tablet') {
        font-size: 22px;
    }

    @include mq('desktop') {
        font-size: 38px;
    }


    &--accent {
        font-size: 48px;
        line-height: 0.965;
        color: var(--accent-color);

        @include mq('tablet') {
            font-size: 34px;
            line-height: 0.905;
        }

        @include mq('desktop') {
            font-size: 58px;
        }
    }


}

.hero__list {
    display: flex;
}

.hero__item:not(:last-child) {
    margin-right: 8px;

    @include mq('desktop') {
        margin-right: 12px;
    }
}

.button--hero {
    font-size: 8px;
    line-height: 1.3;
    padding: 7px 14px;

    text-decoration: none;
    display: block;

    @include mq('desktop') {
        font-size: 14px;
        padding: 11px 24px;
    }
}


.bio-eco {
    @include mq('mobile-only') {
        display: none;
    }

    @include mq('tablet') {
        position: absolute;
        bottom: 16px;
        left: 32px;

        display: flex;
        z-index: 10;

        animation: Appereance-milk-tablet 2s 1 ease-in-out;
    }

    @include mq('desktop') {
        bottom: 51px;
        left: 16px;

        animation: Appereance-milk-desktop 2s 1 ease-in-out;
    }



    &__img {
        @include mq('desktop') {
            width: 213px;
            height: 216px;
        }
    }

    &__container {
        width: 114px;
        padding-top: 41px;
        padding-bottom: 40px;

        @include mq('desktop') {
            width: 200px;
            padding-top: 68px;
            padding-bottom: 24px;
        }
    }

    &__description {
        font-size: 8px;
        line-height: 1.5;
        color: var(--white-color);
        margin-bottom: 8px;

        @include mq('desktop') {
            font-size: 14px;
            line-height: 1.4;
            margin-bottom: 20px;
        }
    }
}


.bio-eco__link {
    display: block;
    width: 18px;
    height: 18px;
    background-color: var(--white-color);
    border-radius: 50%;
    color: var(--accent-color);

    display: flex;
    align-items: center;
    justify-content: center;

    @include mq('desktop') {
        width: 26px;
        height: 26px;
    }

    transition: background-color 250ms var(--transition-timing),
    color 250ms var(--transition-timing);

    &:hover,
    &:focus {
        background-color: var(--accent-color);
        color: var(--white-color);
    }
}

.bio-eco__icon {
    fill: currentColor;

    @include mq('desktop') {
        width: 7px;
        height: 6px;
    }
}

.hero__image {
    opacity: 0;
    animation: ice-cream-anime-hero 200ms 1 ease-in-out;
    animation-delay: 2s;
    animation-fill-mode: forwards;
    position: absolute;
    bottom: 0px;
    left: calc(117px + (100% - 320px)/2);
    z-index: 10;

    @include mq('tablet') {
        bottom: 0px;
        left: 322px;
        width: 143px;
        height: 336px;
    }

    @include mq('desktop') {
        left: 636px;
        width: 236px;
        height: 557px;
    }
}

.thumb-ice-cream {
    display: inline-block;
    position: relative;
    width: 100%;
    height: 100%;
}

%test {
    z-index: 9;
    position: absolute;
    opacity: 0;



    bottom: 0px;
    left: calc(117px + (100% - 320px)/2);


    @include mq('tablet') {
        bottom: 0px;
        left: 322px;
        width: 143px;
        height: 336px;
    }

    @include mq('desktop') {
        left: 636px;
        width: 236px;
        height: 557px;
    }
}

.ice-cream {
    $time-anime: 2s;

    &7 {
        transform: translate(0, 34.5%);
        @extend %test;
        animation: ice-cream-anime7 $time-anime 1 ease-in-out;


    }


    &6 {
        transform: translate(0, 19%);
        @extend %test;
        animation: ice-cream-anime $time-anime 1 ease-in-out;
        animation-delay: 100ms;

    }

    &5 {
        transform: translate(0, 11%);
        @extend %test;
        animation: ice-cream-anime $time-anime 1 ease-in-out;
        animation-delay: 200ms;

    }

    &4 {
        transform: translate(0, 1.5%);
        @extend %test;
        animation: ice-cream-anime $time-anime 1 ease-in-out;
        animation-delay: 300ms;

    }

    &3 {
        transform: translate(0, -9%);
        @extend %test;
        animation: ice-cream-anime $time-anime 1 ease-in-out;
        animation-delay: 400ms;

    }

    &2 {
        transform: translate(0, -23%);
        @extend %test;
        animation: ice-cream-anime $time-anime 1 ease-in-out;
        animation-delay: 500ms;

    }

    &1 {
        transform: translate(0, -38%);
        @extend %test;
        animation: ice-cream-anime $time-anime 1 ease-in-out;
        animation-delay: 600ms;

    }

}

.hero__circle {
    width: 346px;
    height: 344px;
    background-color: var(--secondary-accent-color);
    border-radius: 50%;

    position: absolute;
    left: calc(36px + (100% - 320px)/2);
    bottom: 75px;

    @include mq('tablet') {
        bottom: 40px;
        left: 248px;
        width: 335px;
        height: 333px;
    }

    @include mq('desktop') {
        bottom: 76px;
        left: 517px;

        width: 534px;
        height: 534px;
    }
}

.amount {
    @include mq('mobile-only') {
        display: none;
    }

    @include mq('tablet') {
        position: absolute;
        top: 92px;
        right: 32px;

        animation: Appereance-amount-tablet 2s 1 ease-in-out;


        &__item {
            position: relative;
            width: 56px;
            height: 43px;
            display: flex;
            flex-direction: column;
            padding-right: 8px;
        }

        &__item:not(:last-child) {
            margin-bottom: 16px;

        }
    }

    @include mq('desktop') {
        top: 152px;
        right: 16px;

        animation: Appereance-amount-desktop 2s 1 ease-in-out;

        &__item {
            width: 96px;
            height: 76px;
            padding-right: 16px;
        }

        &__item:not(:last-child) {
            margin-bottom: 15px;
        }
    }
}

.amount__description {
    font-weight: 400;
    font-size: 8px;
    line-height: 1.25;

    color: var(--white-color);
    text-align: right;

    @include mq('desktop') {
        font-size: 14px;
    }


}

.amount__accent {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 28px;
    line-height: 1.14;
    text-align: right;


    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: var(--accent-color);

    z-index: 10;

    @include mq('desktop') {
        font-size: 46px;
        line-height: 1.15;
    }
}



.amount__description::before {
    content: '';
    display: flex;
    width: 16px;
    height: 16px;
    background-color: var(--white-color);
    border-radius: 50%;

    position: absolute;

    top: 0;
    right: 0;

    @include mq('desktop') {
        width: 30px;
        height: 30px;
    }
}






.enticing {
    @include mq('mobile-only') {
        display: none;
    }

    &__image {
        @include mq('tablet') {
            position: absolute;
            right: 32px;
            bottom: 0;
        }

        @include mq('desktop') {
            right: 16px;

            width: 304px;
            height: 241px;
        }



        animation: Appereance 2s 1 ease-in-out,
        swing 5s ease-in-out 5s infinite forwards;


    }
}

@keyframes Appereance {
    from {
        bottom: -100%;
    }

    to {
        bottom: 0px;
    }
}

@keyframes Appereance-milk-tablet {

    from {
        bottom: -100%;
    }

    to {
        bottom: 16px;
    }
}

@keyframes Appereance-milk-desktop {
    from {
        bottom: -100%;
    }

    to {
        bottom: 51px;
    }
}

@keyframes Appereance-container-tablet {

    from {
        left: -100%;
    }

    to {
        left: 32px;
    }
}

@keyframes Appereance-container-desktop {
    from {
        left: -100%;
    }

    to {
        left: 16px;
    }
}

@keyframes Appereance-amount-tablet {

    from {
        right: -100%;
    }

    to {
        right: 32px;
    }
}

@keyframes Appereance-amount-desktop {
    from {
        right: -100%;
    }

    to {
        right: 16px;
    }
}




@keyframes swing {
    0% {

        transform: rotate(8deg);
    }

    4% {

        transform: rotate(-8deg);
    }

    8% {

        transform: rotate(6deg);
    }

    12% {

        transform: rotate(-6deg);
    }

    16% {

        transform: rotate(0deg);
    }
}

@keyframes ice-cream-anime {
    0% {
        opacity: 1;

        bottom: 120%;
    }

    80% {
        bottom: 0;
    }

    100% {

        bottom: 0;
        opacity: 1;

    }
}

@keyframes ice-cream-anime7 {
    0% {
        opacity: 1;

        bottom: -100%;
    }

    80% {
        bottom: 0;
    }

    100% {

        bottom: 0;
        opacity: 1;

    }
}

@keyframes ice-cream-anime-hero {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}